import './styles.css';
import debounce from 'lodash.debounce';
import refs from './js/refs';
import tplCountries from './templates/countries.hbs';
// import PNotify from 'pnotify/dist/es/PNotify.js';
// import PNotifyButtons from 'pnotify/dist/es/PNotifyButtons.js';





refs.searchForm.addEventListener('input',debounce(formSearch,500),);


function formSearch(event) {
  const inputValue=event.target.value;
  fetchCountries(inputValue);
}
function fetchCountries(name) {
  fetch(`https://restcountries.eu/rest/v2/name/${name}`)
    .then(response => {
    return response.json()
  }).then(markupCountries)

}

function markupCountries(countries) {
  console.log(countries.length);
  // if(countries.length > 10) {
  //   refs.resultSearch.innerHTML='';
  // }
  // PNotify.notice({
  //   text: "I'm a notice."
  // });
  refs.resultSearch.innerHTML='';
  refs.resultSearch.insertAdjacentHTML('beforeend',tplCountries(countries));
}
